body {
  background-color: #e0d6c4;
}

.header-bar {
  height: 60px;
  background: linear-gradient(90deg, #2f0a0a 0%, brown 50%, #2f0a0a 100%);
  border-bottom: 1px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 5px 8px -3px rgba(0, 0, 0, .56);
}

.header-bar__input {
  width: 240px;
  height: 30px;
  margin-right: 10px;
}

.header-bar__input:focus {
  outline: none;
}

.header-bar__btn {
  height: 30px;
  background-color: orange;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 3px;
  font-weight: 500;
  box-shadow: 3px 5px 10px rgba(0, 0, 0, .5);
}

.header-bar__btn:active {
  color: gold;
  background-color: #c4771a;
}

